/*ON HOVER'S */

.zoom-on-hover {
  max-width: 100%;
  max-height: auto;
  transition: transform 0.3s;
  border: none;
  background: none;
}
.zoom-on-hover:hover {
  -ms-transform: scale(0.2); /* IE 9 */
  -webkit-transform: scale(0.2); /* Safari 3-8 */
  transform: scale(1.1);
  /*background: white;
    opacity: 0.8;*/
  cursor: pointer;
}

.img-wrap-rooftop {
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url('../images/concepts/landscape--rooftop_redigert.png');
  background-size: cover;
  width: 90%;
  height: 100%;
  position: relative;
  float: left;
  color: white;
  font-size: 2em;
}

.img-wrap-rooftop:hover,
.img-wrap-rainbed:hover,
.img-wrap-indoor:hover {
  -ms-transform: scale(1.05); /* IE 9 */
  -webkit-transform: scale(1.05); /* Safari 3-8 */
  transform: scale(1.05);
  /*background: white;
    opacity: 0.8;*/
  cursor: pointer;
  transition: 0.5s;
}

.img-wrap-rainbed {
